<template>
  <div id="map">
    <div id="container" style="width:100%; height:680px; margin-top: 20px"></div>
    <div class="input-card">
        <!-- <h4>左击地图获取经纬度：</h4> -->
        <el-tag>左击地图获取经纬度：</el-tag>
        <div class="input-item">
            <el-input id="latitude" placeholder="纬度"></el-input>
            <el-input id="longitude" placeholder="经度"></el-input>
            <!-- <input type="text" id="latitude" placeholder="纬度" v-model="latitude"> -->
            <!-- <input type="text" id="longitude" placeholder="经度" v-model="longitude"> -->
        </div>

        <el-button class="btn" type="primary" :plain="true" @click.stop="send()">发送经纬度</el-button>
    </div>
  </div>
</template>

<script>
import AMap from 'AMap'
export default {
  name: 'Map',
  data() {
    return {
      map:{},
      latitude:0,   //纬度
      longitude:0,  //经度
    }
  },
  mounted() {
    this.createMap()
  },
  methods:{
    createMap() {
        this.map = new AMap.Map('container', {
            zoom: 17,//级别
            center: [120.353012, 30.313102],//中心点坐标
            viewMode: '3D'//使用3D视图
        });
        this.map.on('click', function (e) {
            document.getElementById("latitude").value = e.lnglat.getLat()
            this.latitude = e.lnglat.getLat()  //在这设置latitude无法起效
            // console.log(this.latitude)
            document.getElementById("longitude").value = e.lnglat.getLng()
            this.longitude=e.lnglat.getLng()
            // console.log(this.longitude)
        });
    },

    send() {
      this.latitude = document.getElementById("latitude").value
      this.longitude=document.getElementById("longitude").value
      var myHeaders = new Headers();
      myHeaders.append("Accept", "application/vnd.api+json");
      myHeaders.append("api-key", "kTSTsWDKkOT=L=8NCpERgtAtt2U=");
      myHeaders.append("Content-Type", "text/plain");

      var raw =
        `{\r\n    "data":[\r\n        {\r\n            "res_id":5750,\r\n            "val":${this.latitude}\r\n        }\r\n    ]\r\n}`;

      var requestOptions = {
        method: "POST",
        headers: myHeaders,
        body: raw,
        redirect: "follow",
      };

      fetch(
        "/api/nbiot?imei=864283046848534&obj_id=3306&obj_inst_id=0&mode=1",
        requestOptions
      )
        .then((response) => response.text())
        .then((result) => {
          console.log(result)
          this.alert(result)
        })
        .catch((error) => console.log("error", error));
    },
    alert(result) {
      console.log(this.latitude)
      console.log(this.longitude)
      let errno=JSON.parse(result)
      let info=JSON.parse(result).error
      if (errno==0) {
        this.$message({
          message: `${info}`,
          type: 'success'
        });
      }
      else {
        this.$message.error(`${info}`);
      }
    }
  },
  beforeDestroyed() {
    if(!this.map){
        this.map.destroy();
    }
  }
}
</script>

<style scoped>
#map {
    width: 100vw;
    height: 85vh;
}
.input-card {
  position: absolute;
  left: 85%;
  top: 79%;
}
.btn {
  margin-top:10px;
}
</style>